@import url('https://fonts.googleapis.com/css?family=Great+Vibes|Muli:300,400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mea+Culpa&family=Sedgwick+Ave&display=swap');


h1,h2,h3{
  font-family: 'Mea Culpa', cursive;
  color: #fff;
}
body{
  font-family: 'Muli', sans-serif;
}
ul{
  list-style: none;
  margin-bottom: 0!important;
}
a{
  text-decoration: none;
  transition: all .3s;
}
a:hover{
  text-decoration: none!important;
}
img{
  width: 100%;
}
.section-padding{
  padding: 120px 0;
}

.App {
  text-align: left;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: linear-gradient(to bottom right, #ED7B84 ,#9055FF);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: linear-gradient(to bottom right, #ED7B84 ,#9055FF);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media(max-width:767px){
  .section-padding {
    padding: 80px 0;
}
}
.fixed-navbar {
  position: relative;
  z-index: 5555;
}
.fixed-navbar.active .header,
.fixed-navbar.active .header-style-2,
.fixed-navbar.active .header-style-3{
  position: fixed;
  z-index: 555;
  top: 0;
  opacity: 1;
  left: 0;
  width: 100%;
  background: linear-gradient(to bottom right, #ED7B84 ,#9055FF);
  border: none;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -moz-box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -webkit-box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
}
.fixed-navbar.active .header-style-2,
.fixed-navbar.active .header-style-3{
  background: linear-gradient(to bottom right, #ED7B84 ,#9055FF);
}

.pb-70{
  padding-bottom: 90px!important;
}

.fixed-navbar.active .header ul li a:hover{
  background: -webkit-linear-gradient( #ED7B84 ,#9055FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.fixed-navbar.active .header ul li ul li a:hover{
  background: -webkit-linear-gradient( #ED7B84 ,#9055FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.fixed-navbar.active .header .responsivemenu li a:hover {
  background: -webkit-linear-gradient( #ED7B84 ,#9055FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}