.contact-form button{
    position: relative;
    display: inline-block;
    padding: 0px 30px;
    color: #fff;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 18px;
    transition: 1s;
    overflow: hidden;
    border-radius:20px;
     border: 2px solid #000000;
     border-color: #fff;
     background: linear-gradient( #ED7B84 ,#9055FF);
}
.rsvp-wrap h2, .rsvp-wrap p{
    color: #fff;
}
.contact-form button:hover{
    padding: 0 40px;
    transition: 0.5s;
}
.contact-form button span{
    position: absolute;
    display: block;
}
.contact-form button span:nth-child(1){
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    border-radius: 20px;
    background: linear-gradient(90deg,transparent,#fff);
    -webkit-animation: animate1 1s linear infinite;
            animation: animate1 1s linear infinite;
}
@-webkit-keyframes animate1{
    0%{
        left: -100%;
    }
    50%,100%{
        left: 100%;
    }
}
@keyframes animate1{
    0%{
        left: -100%;
    }
    50%,100%{
        left: 100%;
    }
}
.contact-form button span:nth-child(2){
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    border-radius: 20px;
    background: linear-gradient(180deg,transparent,#fff);
    -webkit-animation: animate2 1s linear infinite;
            animation: animate2 1s linear infinite;
    -webkit-animation-delay: 0.25s;
            animation-delay: 0.25s;
}
@-webkit-keyframes animate2{
    0%{
        top: -100%;
    }
    50%,100%{
        top: 100%;
    }
}
@keyframes animate2{
    0%{
        top: -100%;
    }
    50%,100%{
        top: 100%;
    }
}
.contact-form button span:nth-child(3){
    bottom: 0;
    right: 0;
    width: 100%;
    height: 5px;
    border-radius: 20px;
    background: linear-gradient(270deg,transparent,#fff);
    -webkit-animation: animate3 1s linear infinite;
            animation: animate3 1s linear infinite;
    -webkit-animation-delay: 0.50s;
            animation-delay: 0.50s;
}
@-webkit-keyframes animate3{
    0%{
        right: -100%;
    }
    50%,100%{
        right: 100%;
    }
}
@keyframes animate3{
    0%{
        right: -100%;
    }
    50%,100%{
        right: 100%;
    }
}


.contact-form button span:nth-child(4){
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    border-radius: 20px;
    background: linear-gradient(360deg,transparent,#fff);
    -webkit-animation: animate4 1s linear infinite;
            animation: animate4 1s linear infinite;
    -webkit-animation-delay: 0.75s;
            animation-delay: 0.75s;
}
@-webkit-keyframes animate4{
    0%{
        bottom: -100%;
    }
